import * as React from "react";

function SvgCypressLogo(props: any) {
  return (
    <svg
      width="403px"
      height="135px"
      viewBox="0 0 403 135"
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      {...props}
    >
      <image
        width={403}
        height={135}
        xlinkHref="data:image/png;base64,"
        fill="none"
        fillRule="evenodd"
      />
    </svg>
  );
}

export default SvgCypressLogo;
